<template>
	<div id="coupon-market">
		<public-header title="特价券集市"></public-header>
		<div class="content font-14">
			<div class="bg-danger flex-center padding-10-b">
				<ul class="tips flex-center">
					<li class="flex margin-10-r">
						<i class="icon icon-check margin-5-r"></i>
						<span>优惠可叠加</span>
					</li>
					<li class="flex">
						<i class="icon icon-check margin-5-r"></i>
						<span>购买赠好友</span>
					</li>
				</ul>
			</div>
			<div class="padding-10-tb padding-15-l font-15 font-500">附近商家</div>
			<ul class="store-list">
				<li class="store-item" v-for="(item, index) in 5" :key="index">
					<div class="store-info flex-lr c-gray">
						<div class="store-logo">
							<img src="https://1.duoxunwl.com/attachment/images/1/2019/06/wnuV2G316n8O18RdI16rr1d02UIj8O.png" class="img-100">
						</div>
						<div class="store-other">
							<div class="flex-lr">
								<div class="title-score-distance flex">
									<div class="store-title c-default ellipsis">华莱士炸鸡汉堡</div>
									<div class="flex-center font-12 margin-5-l">
										<i class="icon icon-favorfill c-danger font-12"></i>
										<span class="c-danger">4.73</span>
										<span class="padding-5-lr"> | </span>
										<span>424m</span>
									</div>
								</div>
								<div class="flex">
									<span>1种在售</span>
									<i class="icon icon-right font-12"></i>
								</div>
							</div>
							<ul class="store-activity">
								<li class="activity-item">35减12</li>
								<li class="activity-item">25减8</li>
							</ul>
						</div>
					</div>
					<ul class="coupon-list margin-15-t">
						<li class="coupon-item" v-for="(item, index) in 5" :key="index">
							<div class="coupon-img">
								<img src="https://1.duoxunwl.com/attachment/images/1/2019/06/wnuV2G316n8O18RdI16rr1d02UIj8O.png" alt="" class="img-100">
								<span class="num">2张</span>
							</div>
							<div class="coupon-info">
								<div>
									<div class="w-100 ellipsis font-15">优惠券名称·2张</div>
									<div class="coupon-tips">每份低至￥13.55</div>
								</div>
								<div class="price-wrap">
									<div class="c-danger font-12 flex">
										<span>￥</span>
										<span class="font-14">20.55</span>
										<span class="line-through c-gray margin-5-l">￥40.00</span>
									</div>
									<router-link tag="div" to="" class="btn-buy">抢</router-link>
								</div>
							</div>
						</li>
					</ul>
				</li>
			</ul>

		</div>
		<transition name="loading">
			<iloading v-show="showLoading"></iloading>
		</transition>
	</div>
</template>

<script>
	import PublicHeader from '@/components/header'
	export default {
		data() {
			return {
				showLoading: false
			}
		},
		components: {
			PublicHeader,
		},
		methods: {

		},
		mounted() {

		}
	}
</script>

<style >
	#coupon-market .tips{
		padding: 5px 15px;
		border-radius: 100px;
		background-color: #ECA27D;
	}
	#coupon-market .tips .icon{
		width: 14px;
		height: 14px;
		line-height: 14px;
		border-radius: 50%;
		text-align: center;
		font-size: 12px;
		color: #ECA27D;
		background-color: #fff;

	}
	#coupon-market .store-list .store-item{
		position: relative;
		margin-bottom: 10px;
		padding: 15px;
		background-color: #fff;
	}
	#coupon-market .store-list .store-item:last-child{
		margin-bottom: 0px;
	}
	#coupon-market .store-list .store-item .store-logo{
		width: 40px;
		height: 40px;
		border-radius: 5px;
		overflow: hidden;
	}
	#coupon-market .store-list .store-item .store-other{
		position: absolute;
		top: 15px;
		right: 15px;
		left: 65px;
		height: 40px;
		display: flex;
		flex-flow: column nowrap;
		justify-content: space-between;
	}
	#coupon-market .store-list .store-item .store-other .title-score-distance{
		width: 70%;
		overflow: hidden;
	}
	#coupon-market .store-list .store-item .store-other .store-title{
		max-width: 60%;
	}
	#coupon-market .store-list .store-item .store-other .store-activity{
		white-space: nowrap;
		overflow: hidden;
		font-size: 0px;
	}
	#coupon-market .store-list .store-item .store-other .store-activity .activity-item{
		display: inline-block;
		padding: 2px 5px;
		margin-right: 5px;
		border-radius: 3px;
		border: 1px solid #D9D9D9;
		font-size: 12px;
		max-width: 60px;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
	}
	#coupon-market .store-list .store-item .coupon-list{
		overflow-x: scroll;
		overflow-y: hidden;
		-webkit-overflow-scrolling: touch;
		overflow-scrolling:touch;
		white-space: nowrap;
	}
	#coupon-market .store-list .store-item .coupon-list::-webkit-scrollbar {
		display: none;
	}
	#coupon-market .store-list .store-item .coupon-list .coupon-item{
		display: inline-block;
		position: relative;
		padding: 10px;
		background-color: #F8F8F8;
		width: 80%;
		border-radius: 5px;
		margin-right: 10px;
	}
	#coupon-market .store-list .store-item .coupon-list .coupon-item:last-child{
		margin-right: 0px;
	}
	#coupon-market .store-list .store-item .coupon-list .coupon-item .coupon-img{
		position: relative;
		width: 80px;
		height: 80px;
		border-radius: 5px;
		overflow: hidden;
	}
	#coupon-market .store-list .store-item .coupon-list .coupon-item .num{
		position: absolute;
		right: 5px;
		top: 5px;
		background-color: rgba(132,132,132,0.7);
		color: #fff;
		font-size: 12px;
		padding: 2px 5px;
		border-radius: 3px;
	}
	#coupon-market .store-list .store-item .coupon-list .coupon-item .coupon-info{
		position: absolute;
		top: 10px;
		right: 10px;
		left: 100px;
		height: 80px;
		display: flex;
		flex-flow: column nowrap;
		justify-content: space-between;
	}
	#coupon-market .store-list .store-item .coupon-list .coupon-item .coupon-info .coupon-tips{
		display: inline-block;
		padding: 3px 5px;
		margin-top: 10px;
		border-radius: 3px;
		color: #FF2D4B;
		border: 1px solid #ff2d4b;
		font-size: 12px;
	}
	#coupon-market .store-list .store-item .coupon-list .coupon-item .coupon-info .price-wrap{
		display: flex;
		align-items: flex-end;
		justify-content: space-between;
		flex-flow: row nowrap;
	}
	#coupon-market .store-list .store-item .coupon-list .coupon-item .coupon-info .price-wrap .btn-buy{
		padding: 8px 15px 8px 12px;
		border-radius: 100px;
		font-style: italic;
		font-weight: bold;
		font-size: 15px;
		color: #fff;
		background-color: #ff2d4b;
	}
</style>



